html, body, #root {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    height: 100%;
    margin: 0;
}

.main-container {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.search {
    outline: none;
    padding: 20px 2%;
    border-radius: 20px;
    border: none;
    margin-bottom: 5%;
    background: rgba(43, 42, 42, 0.85);
    width: 300px;
    color: white;
}

.search::placeholder{
    color: white;
}

p {
    margin-top: 10px;
    text-transform: uppercase;
    letter-spacing: 0.05em;
}

@media only screen and (max-width: 600px) {
    .search {
        padding: 20px 15%;
    }
}

.card-container{
    width: auto;
    height: auto;
    padding: 30px 20px;
    background-color: #3498db;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    z-index: 2;

}

.right,.left,.mid{
    padding: 5px;
    margin: 0 20px;
    text-align: center;

}

.bb{
    height: 100vh;
    width: 100%;
    position: absolute;
    z-index: -1;
    object-fit: cover;
}

.mid-container{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; 
}

.btn {
    background: #3498db;
    -webkit-border-radius: 60;
    -moz-border-radius: 60;
    border-radius: 60px;
    -webkit-box-shadow: 3px 3px 11px #666666;
    -moz-box-shadow: 3px 3px 11px #666666;
    box-shadow: 3px 3px 11px #666666;
    color: #ffffff;
    font-size: 20px;
    padding: 15px 30px 15px 30px;
    text-decoration: none;
    outline: none;
    border: none;
    cursor: pointer;
    margin-top: 50px;
    font-family: inherit;
    letter-spacing: 0.05em;
  }
  
  .btn:hover {
    background: #3cb0fd;
    text-decoration: none;
  }